.Header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 25px;
  overflow: hidden;
  z-index: 1;
  // background-color: black;
}

.logo {
  display: flex;
  align-items: center;
  img {
    height: 50px;
    margin-right: 10px;
  }
  h2 {
    color: white;
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
  }
}
.menuWrap {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: flex-end;
  gap: 3%;
}
.navWrap {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: flex-end;
  gap: 3%;
  .navItem {
    padding: 15px 15px;
    font-weight: 600;
    font-size: 16px;
    line-height: 12px;
    color: rgba(#fff, 0.5);
    text-decoration: none;
    &.active,
    &:hover {
      color: white;
    }
  }
}
