.About {
  width: 100%;
  position: relative;
  overflow: hidden;
}
.banner {
  height: 100vh;
  width: 100%;
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center bottom;
  }
}
.bannerTextWrap {
  position: absolute;
  top: 5vh;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
  max-width: 1000px;
  // p {
  //   font-weight: 400;
  //   font-size: 20px;
  //   line-height: 140%;
  //   margin-top: 40px;
  // }
}
.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 150px;
}
.mainContent {
  width: 100%;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}
.getNodeWrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  .buttonWrap {
    display: flex;
    gap: 15px;
  }
}
.getNodeCombo {
  max-width: 900px;
}
.statsWrap {
  width: 100%;
}
.statsItem {
  width: 100%;
  padding: 25px 0;
  background-color: black;
  h2 {
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    /* identical to box height, or 18px */
    text-align: center;
    color: #e7e7e7;
    opacity: 0.5;
    margin-bottom: 15px;
  }
  p {
    font-weight: 600;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #e7e7e7;
  }
}
.nftIntro {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.nftIntroItem {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 50px;
  img {
    width: 40%;
  }
  &.justifyRight {
    justify-content: flex-end;
  }
}
.nftIntroAction {
  width: 40%;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  gap: 25px;
  h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 100%;
    /* identical to box height, or 24px */
    color: #e7e7e7;
  }
  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
  }
  &.alignRight {
    text-align: right;
    align-items: flex-end;
  }
}
.pointsSystemContainer {
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
.pointsSystemWrap {
  width: 100%;
  height: 100vh;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
.saleWrap {
  max-width: 1400px;
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
  gap: 30px;
  img {
    width: 100%;
  }
}
.saleTextWrap {
  display: flex;
  align-items: center;
  gap: 10px;
  img {
    height: 50px;
    width: auto;
  }
  .vbar {
    border: none;
    border-right: 1px solid rgba(white, 0.8);
    height: 40px;
  }
  .saleText {
    font-size: 36px;
  }
}
.buyButton {
  background: white;
  color: #3e44cf;
}
